@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
body {
  font-family: 'Roboto', sans-serif;
  background-color: #fafafa;
}
.weather-component .item{border-radius:5px;padding:20px;margin:auto;margin-top:10px;max-width:900px;background-color:white;box-shadow:5px 5px 10px #f3f3f3, -5px -5px 10px #f2f2f2}.weather-component .item .date{font-weight:bold;font-size:18px;padding:10px;border-bottom:1px solid #eee}.weather-component .item .content{display:flex;align-items:center}.weather-component .item .content .icon{height:100px;width:100px;margin-right:15px;border-radius:50%}.weather-component .item .content .temp{font-weight:bold}.weather-component .item .content .min-temp{font-weight:lighter}.weather-component .item .content .max-temp{font-weight:lighter}.weather-component .error{margin:auto;text-align:center;padding:20px}

.home-component .form{display:flex;background-color:white;border-radius:5px;max-width:940px;margin:auto;margin-top:25px;transition:0.2s;box-shadow:5px 5px 10px #e4e4e4, -5px -5px 10px #f2f2f2}.home-component .form button{background-color:transparent;border:0;outline:none;padding:20px;transition:.2s;border-top-right-radius:5px;border-bottom-right-radius:5px;cursor:pointer}.home-component .form button:hover{background-color:#eee}.home-component .form input{background-color:transparent;border:0;outline:none;width:100%;padding:20px}.home-component .form:focus-within{box-shadow:5px 5px 50px #e4e4e4, -5px -5px 10px #f2f2f2}.home-component .loading{margin:auto;text-align:center;padding:20px}.home-component .footer{margin:auto;text-align:center;padding:20px}

